/**
 * Transaction Context
 * 
 * Manages transaction state and provides CRUD operations.
 * Also handles summary data for charts and analytics.
 */

import React, { createContext, useContext, useState, useCallback } from 'react';
import { toast } from 'react-toastify';
import api from '../services/api';

// Create context
const TransactionContext = createContext(null);

/**
 * Custom hook to use transaction context
 * @returns {Object} Transaction context value
 */
export const useTransactions = () => {
  const context = useContext(TransactionContext);
  if (!context) {
    throw new Error('useTransactions must be used within a TransactionProvider');
  }
  return context;
};

/**
 * Transaction Provider Component
 * 
 * Manages transactions and provides methods for CRUD operations.
 */
export const TransactionProvider = ({ children }) => {
  const [transactions, setTransactions] = useState([]);
  const [summary, setSummary] = useState({
    balance: 0,
    income: 0,
    expenses: 0,
    expensesByCategory: [],
    trend: [],
  });
  const [loading, setLoading] = useState(false);
  const [pagination, setPagination] = useState({
    page: 1,
    limit: 20,
    total: 0,
    totalPages: 0,
  });

  /**
   * Fetch transactions with optional filters
   * 
   * @param {Object} params - Query parameters
   * @returns {Promise<void>}
   */
  const fetchTransactions = useCallback(async (params = {}) => {
    setLoading(true);
    try {
      const response = await api.get('/transactions', { params });
      
      if (response.data.success) {
        setTransactions(response.data.data);
        setPagination({
          page: response.data.page,
          limit: response.data.limit,
          total: response.data.total,
          totalPages: response.data.totalPages,
        });
      }
    } catch (error) {
      toast.error('Failed to fetch transactions');
    } finally {
      setLoading(false);
    }
  }, []);

  /**
   * Fetch transaction summary for charts
   * 
   * @param {string} period - 'week', 'month', or 'year'
   * @returns {Promise<void>}
   */
  const fetchSummary = useCallback(async (period = 'month') => {
    try {
      const response = await api.get('/transactions/summary', {
        params: { period },
      });
      
      if (response.data.success) {
        // Normalize the data structure for the Dashboard
        const data = response.data.data;
        setSummary({
          balance: data.balance || 0,
          income: data.totalIncome || 0,
          expenses: data.totalExpense || 0,
          expensesByCategory: data.expensesByCategory || [],
          trend: data.monthlySummary || [],
        });
      }
    } catch (error) {
      toast.error('Failed to fetch summary');
    }
  }, []);

  /**
   * Create a new transaction
   * 
   * @param {Object} transactionData - Transaction data
   * @returns {Promise<Object|null>} Created transaction or null
   */
  const createTransaction = async (transactionData) => {
    try {
      const response = await api.post('/transactions', transactionData);
      
      if (response.data.success) {
        // Add to local state
        setTransactions((prev) => [response.data.data, ...prev]);
        
        // Show budget alert if present
        if (response.data.budgetAlert) {
          const alert = response.data.budgetAlert;
          if (alert.type === 'exceeded') {
            toast.error(alert.message, { autoClose: 5000 });
          } else {
            toast.warning(alert.message, { autoClose: 5000 });
          }
        } else {
          toast.success('Transaction added successfully');
        }
        
        return response.data.data;
      }
    } catch (error) {
      const message = error.response?.data?.message || 'Failed to create transaction';
      toast.error(message);
      return null;
    }
  };

  /**
   * Update an existing transaction
   * 
   * @param {string} id - Transaction ID
   * @param {Object} transactionData - Updated transaction data
   * @returns {Promise<Object|null>} Updated transaction or null
   */
  const updateTransaction = async (id, transactionData) => {
    try {
      const response = await api.put(`/transactions/${id}`, transactionData);
      
      if (response.data.success) {
        // Update local state
        setTransactions((prev) =>
          prev.map((t) => (t._id === id ? response.data.data : t))
        );
        toast.success('Transaction updated successfully');
        return response.data.data;
      }
    } catch (error) {
      const message = error.response?.data?.message || 'Failed to update transaction';
      toast.error(message);
      return null;
    }
  };

  /**
   * Delete a transaction
   * 
   * @param {string} id - Transaction ID
   * @returns {Promise<boolean>} Success status
   */
  const deleteTransaction = async (id) => {
    try {
      const response = await api.delete(`/transactions/${id}`);
      
      if (response.data.success) {
        // Remove from local state
        setTransactions((prev) => prev.filter((t) => t._id !== id));
        toast.success('Transaction deleted');
        return true;
      }
    } catch (error) {
      const message = error.response?.data?.message || 'Failed to delete transaction';
      toast.error(message);
      return false;
    }
  };

  /**
   * Export transactions as CSV
   * 
   * @param {Object} params - Filter parameters
   */
  const exportCSV = async (params = {}) => {
    try {
      const response = await api.get('/transactions/export/csv', {
        params,
        responseType: 'blob',
      });

      // Create download link
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'transactions.csv');
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);

      toast.success('Transactions exported successfully');
    } catch (error) {
      toast.error('Failed to export transactions');
    }
  };

  // Context value
  const value = {
    transactions,
    summary,
    loading,
    pagination,
    fetchTransactions,
    fetchSummary,
    createTransaction,
    updateTransaction,
    deleteTransaction,
    exportCSV,
  };

  return (
    <TransactionContext.Provider value={value}>
      {children}
    </TransactionContext.Provider>
  );
};

export default TransactionContext;
