/**
 * API Service
 * 
 * Axios instance with interceptors for JWT authentication
 * and centralized error handling.
 */

import axios from 'axios';

// Create axios instance with default config
const api = axios.create({
  baseURL: process.env.REACT_APP_API_URL || '/api',
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: true, // Send cookies with requests
});

/**
 * Request Interceptor
 * 
 * Adds JWT token to Authorization header if available.
 */
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

/**
 * Response Interceptor
 * 
 * Handles common error responses and token expiration.
 */
api.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    // Handle 401 Unauthorized
    if (error.response?.status === 401) {
      // Token expired or invalid
      localStorage.removeItem('token');
      
      // Only redirect if not already on login page
      if (window.location.pathname !== '/login') {
        window.location.href = '/login';
      }
    }
    
    // Handle network errors
    if (!error.response) {
      error.message = 'Network error. Please check your connection.';
    }
    
    return Promise.reject(error);
  }
);

/**
 * API Methods
 * 
 * Convenience methods for common operations.
 */
export const authAPI = {
  register: (data) => api.post('/auth/register', data),
  login: (data) => api.post('/auth/login', data),
  logout: () => api.post('/auth/logout'),
  getMe: () => api.get('/auth/me'),
  updateProfile: (data) => api.put('/auth/profile', data),
  updatePassword: (data) => api.put('/auth/password', data),
};

export const transactionAPI = {
  getAll: (params) => api.get('/transactions', { params }),
  getOne: (id) => api.get(`/transactions/${id}`),
  create: (data) => api.post('/transactions', data),
  update: (id, data) => api.put(`/transactions/${id}`, data),
  delete: (id) => api.delete(`/transactions/${id}`),
  getSummary: (period) => api.get('/transactions/summary', { params: { period } }),
  exportCSV: (params) => api.get('/transactions/export/csv', {
    params,
    responseType: 'blob',
  }),
};

export const budgetAPI = {
  getAll: () => api.get('/budgets'),
  getWithStatus: () => api.get('/budgets/status/all'),
  getAlerts: () => api.get('/budgets/alerts'),
  create: (data) => api.post('/budgets', data),
  update: (category, data) => api.put(`/budgets/${category}`, data),
  delete: (category) => api.delete(`/budgets/${category}`),
};

export default api;
